import { motion } from "framer-motion";
import React from "react";
import home1 from "../img/home-1.jpg";
import { About, Description, Hide, Image } from "../styles";
import { titleAnim, fade, photoAnim } from "../animation";
import Wave from "./Wave";

const AboutUsSection = () => {
  return (
    <About className="about">
      <Description className="description">
        <motion.div>
          <Hide>
            <motion.h2 variants={titleAnim}>We work to make</motion.h2>
          </Hide>
          <Hide>
            <motion.h2 variants={titleAnim}>
              your <span>dreams</span> come
            </motion.h2>
          </Hide>
          <Hide>
            <motion.h2 variants={titleAnim}>true.</motion.h2>
          </Hide>
        </motion.div>
        <motion.p variants={fade}>
        Our mission is to help the client express their own taste and lifestyle while creating spaces of appropriate scale, historical definition, and comfort.
        From English traditional to spare, modernist space, KA Design Group's design philosophy is to approach each project as having its own custom look dictated by the client's needs, vision and aesthetic. 
        </motion.p>
        <motion.button variants={fade}>Contact us</motion.button>
      </Description>
      <Image className="image">
        <motion.img variants={photoAnim} src={home1} alt="" />
      </Image>
      <Wave />
    </About>
  );
};

export default AboutUsSection;
